.wrapper {
  height: 100vh;
  background-image: url("../images/background.jpg");
  background-size: 100% 100%;
}

.title {
  font-family: 'Monoton', cursive;
  text-align: center;
  padding-top: 50px;
}

.title h1 {
  font-size: 4em;
}

.play {
  margin-top: 100px;
  text-align: center;
  display: grid;
  place-items: center;
}

.play button {
  outline: none;
}

.playbtn {
  font-family: 'Bungee', cursive;
  font-size: 4em;
  border: none;
  background-color: transparent;
  color: #B22222;
  text-shadow: 2px 2px black;
  cursor: pointer;
}

.board {
  display: grid;
  grid-template: repeat(3, 130px)/repeat(3, 130px);
}

.box1, .box2, .box3, .box4, .box5, .box6, .box7, .box8, .box9 {
  border: 4px solid black;
  display: grid;
  place-items: center;
}

.box1 {
  grid-row: 1;
  grid-column: 1/2;
  border-top: none;
  border-left: none;
}

.box2 {
  grid-row: 1;
  grid-column: 2/3;
  border-top: none;
}

.box3 {
  grid-row: 1;
  grid-column: 3/4;
  border-top: none;
  border-right: none;
}

.box4 {
  grid-row: 2;
  grid-column: 1/2;
  border-left: none;
}

.box5 {
  grid-row: 2;
  grid-column: 2/3;
}

.box6 {
  grid-row: 2;
  grid-column: 3/4;
  border-right: none;
}

.box7 {
  grid-row: 3;
  grid-column: 1/2;
  border-left: none;
  border-bottom: none;
}

.box8 {
  grid-row: 3;
  grid-column: 2/3;
  border-bottom: none;
}

.box9 {
  grid-row: 3;
  grid-column: 3/4;
  border-right: none;
  border-bottom: none;
}

.board button {
  outline: none;
}

.inputbtn {
  font-family: 'Bungee', cursive;
  font-size: 4em;
  background-color: transparent;
  color: #B22222;
  animation: displaystyle 1s;
}

@-webkit-keyframes displaystyle {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes displaystyle {
  0% {
   transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 800px) {
  .board {
    display: grid;
    grid-template: repeat(3,120px)/repeat(3,130px);
  }
  .title h1 {
    font-size: 2em;
  }
}
